<h2 id="introduction" tabindex="-1"><a class="header-anchor" href="#introduction" aria-hidden="true">#</a> Introduction</h2>
<p>As project work you should work in pairs to create a platform per the instructions on this page. The platform will consist of:</p>
<ul>
<li>A relational database that stores the resources created on the platform</li>
<li>A none-relational database to store temporary data (e.g. sessions) on the platform</li>
<li>A web application that:
<ul>
<li>Web browsers can use to fetch webpages from (i.e. function as a website)</li>
<li>Exposes a REST API clients (e.g. smartphones) can use to work with the resources on the platform</li>
</ul>
</li>
<li>A Single-Page Application through which users can work with the resources on the platform</li>
</ul>
<p>You have a lot of freedom when it comes to the design and functionality of the platform, but the following requirements exist:</p>
<ul>
<li>There should exist at least three different type of resources on the platform:
<ul>
<li>One of the resource types must represent <em>accounts</em> users can create and login to</li>
<li>The other two resource types should somehow have a relation to accounts (direct or indirect), i.e. a created resource should be owned by/belong to an account</li>
</ul>
</li>
<li>Regarding CRUD (Create, Retrieve, Update, Delete) operations on the resources:
<ul>
<li>All three type of resources should support Create and Retrieve operations</li>
<li>Implementing Update and Delete operations for the website is an extra task you can do at the end of the project work to get a higher grade</li>
</ul>
</li>
</ul>
<p>Use whichever type of resources you want in addition to accounts. Some typical (boring) examples are:</p>
<ul>
<li><em>Threads</em> logged in users can create and that other logged in users can write <em>posts</em> to</li>
<li><em>Blog posts</em> logged in users can create and that other logged in users can write <em>comments</em> on</li>
<li><em>Todo lists</em> logged in users can create and add <em>todo items</em> to</li>
</ul>
<p>Examples of more specific platforms are:</p>
<ul>
<li>For a <strong>Car Insurance Company</strong>: <em>Insurance Plans</em> (price, info about what's included in the plan, etc.) users' <em>Cars</em> are connected to</li>
<li>For a <strong>Rescue Service</strong>: <em>Accidents</em> they have attended, and <em>Reported Work Hours</em> by the personnel</li>
<li>For a <strong>Football Club</strong>: <em>Teams</em> in the club, and <em>Players</em> in each team</li>
</ul>
<div class="custom-container tip"><p class="custom-container-title">Finding a good problem to solve</p>
<p>Most good problems obvious to programmers have already been solved by other programmers. To find a good problem to solve as project in this course, you usually need to explore domains you are unfamiliar with. Ask your friends and family if they have a need of a platform of some kind. Someone working within elderly care might have a problem you can solve with a platform implemented in this course? Or someone who likes to walk in the nature? Or someone helping homeless people? Etc.</p>
</div>
<p>Implement the platform using the technologies taught in this course. In addition to implementing the web application, you should also write a report describing how the platform works and how it has been implemented. The file <a href="files/project-report-template.docx">project-report-template.docx</a> contains a template with further instructions on this. Your report will be a living document throughout the course, meaning that you will continuously improve and add content to it.</p>
<p>On the <RouterLink to="/courses/web-development-advanced-concepts/project-grading-guidelines/">Project Grading Guidelines</RouterLink> page you find some guidelines for how your project work will be graded. You are recommended to read through it every now and then.</p>
<p>To help you, we have divided the project work into smaller parts that should be completed in order. The rest of this page contains descriptions of these smaller parts. Before you start working on the first part you are recommended to read through all the parts, which will give you a good overview of the work that lies ahead.</p>
<p>Good luck!</p>
<h2 id="installing-required-software" tabindex="-1"><a class="header-anchor" href="#installing-required-software" aria-hidden="true">#</a> Installing required software</h2>
<p><strong>If you work on the computers in E1206, E2432, E2433 or E4205</strong>, all required software should already be installed for you, so you should not need to install anything yourself.</p>
<p><strong>If you work on a school computer in any other room</strong>, you need to manually install the required software through the <em>Software Center</em> application (unless someone else has already done that on the specific computer you use).</p>
<p><strong>If you work on your own private computer</strong>, you need to download and install all required software yourself. In this course, we use only free software available for both Windows, Mac and Linux.</p>
<p>The required software is:</p>
<ul>
<li><a href="https://docs.docker.com/get-docker/" target="_blank" rel="noopener noreferrer">Docker<OutboundLink/></a> <br>
A program used for containerization, i.e. a program used for running other programs in an isolated and configurable environment.</li>
</ul>
<div class="custom-container danger"><p class="custom-container-title">Docker Toolbox for Windows</p>
<p>There existed an old version of Docker for Windows called <em>Docker Toolbox</em>. It shouldn't be used anymore, so Windows users, be sure to use <em>Docker Desktop for Windows</em>.</p>
</div>
<div class="custom-container warning"><p class="custom-container-title">About Docker Desktop for Windows</p>
<p>Docker Desktop for Windows can be used with either <em>Windows Subsystems for Linux 2</em> (WSL2) or Hyper-v. If you have Windows 10 Home (as most users), you must use it with WSL2.</p>
</div>
<ul>
<li><a href="https://nodejs.org/en/" target="_blank" rel="noopener noreferrer">Node.js<OutboundLink/></a> <br>
Use whichever version you want, but most likely the newer the better. If you don't want to gamble, go with the latest LTS version.</li>
<li><a href="https://www.npmjs.com/" target="_blank" rel="noopener noreferrer">npm<OutboundLink/></a> <br>
This one is installed along with Node.js, so you do not need to install it separately. However, you might want to <a href="https://docs.npmjs.com/troubleshooting/try-the-latest-stable-version-of-npm" target="_blank" rel="noopener noreferrer">update npm to the latest version<OutboundLink/></a>.</li>
</ul>
<p>The following software tools are not required, but recommended:</p>
<ul>
<li><a href="https://code.visualstudio.com/" target="_blank" rel="noopener noreferrer">Visual Studio Code<OutboundLink/></a> <br>
IDE with good support for writing, running and debugging Node.js and JavaScript code, but use whichever IDE you want.</li>
<li><a href="https://git-scm.com/" target="_blank" rel="noopener noreferrer">Git<OutboundLink/></a> <br>
Version Control System that makes it very convenient to work on the project from different computers.</li>
</ul>
