<h2 id="part-2-project-environment" tabindex="-1"><a class="header-anchor" href="#part-2-project-environment" aria-hidden="true">#</a> Part 2: Project Environment</h2>
<p>Before you start working on this part, you are recommended to:</p>
<ul>
<li>Read/View the following lectures:
<ul>
<li><RouterLink to="/lectures/teamwork/">Teamwork</RouterLink></li>
</ul>
</li>
<li>Read the following short course on Git:
<ul>
<li><RouterLink to="/courses/git/">Git</RouterLink></li>
</ul>
</li>
</ul>
<hr>
<p>The very first thing you should agree on is how you should work together. Especially, you need to some extent document who is doing what in the project, and write that in your report, so the examiner can verify that all group members have contributed ~equally much.</p>
<p>We recommend you to work together per the basis described in the <RouterLink to="/lectures/teamwork/">Teamwork</RouterLink> lecture. That way you will automatically document who has contributed with what by using Git and GitHub issues. But if you want to work together in another way, that is OK too, as long as you can show who has contributed with what in the project.</p>
<p>The <em>Worklog</em> chapter in <a href="files/project-report-template.docx">project-report-template.docx</a> shows an example of how you can report who has contributed with what in the project.</p>
<h2 id="part-3-platform-idea" tabindex="-1"><a class="header-anchor" href="#part-3-platform-idea" aria-hidden="true">#</a> Part 3: Platform idea</h2>
<p>Before you start working on this part, you are recommended to:</p>
<ul>
<li>Read/View the following lectures:
<ul>
<li><RouterLink to="/lectures/report-writing/">Report Writing</RouterLink></li>
</ul>
</li>
</ul>
<hr>
<p>Your first task is to come up with what the platform you will create should do. Try to be creative and create a platform that solves a real-world problem ordinary people are having. Feel free to ask your family and friends for problems they have that could be solved using a platform. Examples of real-world problems could be:</p>
<ul>
<li>I often forget meetings</li>
<li>I often make bets with my friends, they remember the bets I lose so I have to pay them, but I often forget the bets I win, so they never pay me</li>
<li>I like to keep track of which celebrities I've seen, but I rarely remember that</li>
<li>I eat lunches alone, and that's so boring</li>
<li>I need to find someone I can play padel with</li>
</ul>
<p>The only important thing is that your platform can be implemented using at least 3 type of resources (accounts + at least two more connected to accounts), but the more useful it is the better. Optimally you have such a great idea that you will want to launch your platform after the course.</p>
<p>Your platform should be described in the project report, so in this part of the project, you will not do any programming, but only work on your report. You should be able to complete at least the <em>Introduction</em> chapter.</p>
<p>When you're done describing your idea in the report, discuss it with a teacher at one of the lab sessions, or some friends to get some initial feedback on your idea and can improve it. Then you are recommended to upload the current version of your report to Canvas by submitting the assignment <a href="https://ju.instructure.com/courses/4775/assignments/25247" target="_blank" rel="noopener noreferrer">Platform Idea<OutboundLink/></a> to get some feedback on your idea from the examiner.</p>
<h2 id="part-4-setting-up-docker" tabindex="-1"><a class="header-anchor" href="#part-4-setting-up-docker" aria-hidden="true">#</a> Part 4: Setting up docker</h2>
<p>Before you start working on this part you must:</p>
<ul>
<li>Complete and be approved on the <RouterLink to="/courses/web-development-advanced-concepts/laboratory-work/">Laboratory Work</RouterLink>.</li>
</ul>
<hr>
<p>Before you start working on this part, you are recommended to:</p>
<ul>
<li>Read/View the following lectures:
<ul>
<li><RouterLink to="/lectures/docker-basics/">Docker Basics</RouterLink></li>
</ul>
</li>
</ul>
<hr>
<p>The web application should be implemented in Node.js using the Express framework (feel free to use <a href="https://koajs.com/" target="_blank" rel="noopener noreferrer">Koa<OutboundLink/></a> instead of Express if you are willing to learn it (not officially part of this course, so you can't expect to get any help with it from the lab assistants)), and the resources on the platform should be stored in a MySQL database. We could create a Docker container that contains both, but when it comes to containerization it is better that a container just has a single responsibility, e.g. to just run the web application or to just run the database. Among other things, it is much easier to scale a container that has a single responsibility. Therefore, the web application should run in one container, and the database should run in another container.</p>
<h3 id="the-web-application-image" tabindex="-1"><a class="header-anchor" href="#the-web-application-image" aria-hidden="true">#</a> The Web Application image</h3>
<p>Start by creating a new folder to store all the source code for the entire platform. In this document we will refer to this folder as <code>/platform</code> (you should of course use a more unique/descriptive name than <code>platform</code>). This is the folder you want to version control, so here you also want to initialize your Git repository. Then create a sub-folder inside this one to store the source code for the web application. In this document we will refer to that sub-folder as <code>/platform/web-application</code>.</p>
<p>In <code>/platform/web-application</code>, run the following commands:</p>
<ol>
<li><code>npm init --yes</code> - Creates <code>packages.json</code>.</li>
<li><code>npm install express</code> - Registers a dependency on the npm package <code>express</code>.</li>
</ol>
<p>Then create the file <code>/platform/web-application/app.js</code> with the content shown in <FigureNumber /> below.</p>
<Figure caption="The content of the file app.js (Hello World with Express).">
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">const</span> express <span class="token operator">=</span> <span class="token function">require</span><span class="token punctuation">(</span><span class="token string">'express'</span><span class="token punctuation">)</span>

<span class="token keyword">const</span> app <span class="token operator">=</span> <span class="token function">express</span><span class="token punctuation">(</span><span class="token punctuation">)</span>

app<span class="token punctuation">.</span><span class="token function">get</span><span class="token punctuation">(</span><span class="token string">'/'</span><span class="token punctuation">,</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token parameter">request<span class="token punctuation">,</span> response</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
  response<span class="token punctuation">.</span><span class="token function">send</span><span class="token punctuation">(</span><span class="token string">"Hello, World"</span><span class="token punctuation">)</span>
<span class="token punctuation">}</span><span class="token punctuation">)</span>

app<span class="token punctuation">.</span><span class="token function">listen</span><span class="token punctuation">(</span><span class="token number">8080</span><span class="token punctuation">,</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
  console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span><span class="token string">"Web application listening on port 8080."</span><span class="token punctuation">)</span>
<span class="token punctuation">}</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br></div></div></Figure>
<p>Verify that the code you have written so far works by starting the web application using the command <code>node app.js</code> in <code>/platform/web-application</code> and then visit <a href="http://localhost:8080" target="_blank" rel="noopener noreferrer">http://localhost:8080<OutboundLink/></a> in a web browser and verify that you see the text <em>Hello, World</em>. If you do, then it works as it should, and you can stop running the web application.</p>
<p>You just ran the web application as an ordinary program on your own computer. Let's try to run it in a container using Docker instead. To do that we need to tell Docker which environment our code needs to run, and how it should be started. To do that we need to put our code in a Docker image that contains all this information, and then ask Docker to start running that image in a new Docker container.</p>
<p>To specify the environment in the image one usually inherit the environment from an existing image. The <a href="https://hub.docker.com" target="_blank" rel="noopener noreferrer">Docker Hub<OutboundLink/></a> contains a collection of images we can use, and in our case we need an image with Node.js pre-installed. Node.js provides <em>official images</em> that comes with Node.js pre-installed, so try searching for <code>node</code> there and find a suitable image (e.g. <code>node:13.5.0</code>). Then create a file called <code>Dockerfile</code> in <code>/platform/web-application</code> that looks something like what shown in <FigureNumber /> below.</p>
<Figure caption="The content of the Dockerfile in the web application folder.">
<div class="language-text ext-text line-numbers-mode"><pre v-pre class="language-text"><code>FROM node:13.5.0

EXPOSE 8080

WORKDIR /web-application

COPY package*.json ./

RUN npm install

COPY app.js ./

CMD ["node", "app.js"]
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br></div></div></Figure>
<div class="custom-container warning"><p class="custom-container-title">Start Docker Desktop for Windows</p>
<p>Before you use any docker command you must start the Docker Desktop for Windows application.</p>
</div>
<p>Docker can then use the information in <code>Dockerfile</code> to build a new image that contains your source code and Node.js to run it. To do that, run the command <code>docker build --tag=my-web-app:latest .</code> in <code>/platform/web-application</code>. This tells Docker to build the image and to add it to its own collection of images on your computer. At the same time, it will also download the <code>node:13.5.0</code> image from Docker Hub (unless it has already done that).</p>
<p>To run the image in a new container, run the command <code>docker run -p 3000:8080 my-web-app:latest</code>. When the container is running you should be able to send HTTP requests to your web application by opening <a href="http://localhost:3000" target="_blank" rel="noopener noreferrer">http://localhost:3000<OutboundLink/></a> in a web browser on your computer. The command used to start the container told Docker that all incoming traffic to port 3000 on your computer should be forwarded to the container's port 8080.</p>
<p>On Mac and Linux you can stop the container from running by pressing <code>[CTRL]</code> + <code>[C]</code>. On Windows you also need to run the command <code>docker container ls</code> to retrieve the name of the container and then run the command <code>docker container stop theContainerName</code>.</p>
<p>Now you know the basics about creating, running and stopping Docker containers. You probably also want to create a <code>.dockerignore</code> file as described at <a href="https://nodejs.org/en/docs/guides/nodejs-docker-webapp/" target="_blank" rel="noopener noreferrer">https://nodejs.org/en/docs/guides/nodejs-docker-webapp/<OutboundLink/></a>, so you more conveniently can build an image with multiple source files. That page is good reading even if you don't want to create a <code>.dockerignore</code> file, so read it!</p>
<p>Your workflow will kind of be:</p>
<ol>
<li>Make changes to the source code files on your computer.</li>
<li>Build a new image.</li>
<li>Run the new image in a new container.</li>
<li>Test your web application in a web browser.</li>
<li>Stop the container from running.</li>
<li>Restart on #1.</li>
</ol>
<p>To manually carry out these steps each time you want to test changes you make to the source code is quite time consuming and boring. Docker has a solution to this problem: volumes. With volumes you can make a container read files stored on your computer. This can be used to always give the container access to the latest version of your files in a specific folder.</p>
<div class="custom-container tip"><p class="custom-container-title">Volumes on Windows</p>
<p>When using volumes on Windows, the recommendation is to put the volumes (in your case all your source code) directly in the filesystem on the Linux distribution you are running in Windows Subsystem for Linux, instead of in the file system for Windows. Read more about it in <a href="https://docs.docker.com/docker-for-windows/wsl/#best-practices" target="_blank" rel="noopener noreferrer">Docker's best practices<OutboundLink/></a>.</p>
</div>
<div class="custom-container danger"><p class="custom-container-title">Volumes on Windows</p>
<p>If you don't put your source code on the filesystem for your Linux instance, it's is a bit uncleared how to specify paths to folders in Windows. <a href="https://github.com/docker/for-win/issues/6628#issuecomment-635906394" target="_blank" rel="noopener noreferrer">One comment on GitHub<OutboundLink/></a> suggests several different ways to do it, but not all of them have worked for me (Peter) in Docker 3. <code>/host_mnt/c/path/to/folder</code> did work, so that's the one used in the instructions below.</p>
</div>
<p>Using a volume is quite easy. When you start a container with the <code>docker run</code> command you also specify which folders that should be synched using the <code>-v</code> option, like (on Windows)
<code>docker run -v /host_mnt/c/folder/on/host/computer:/folder/in/container</code> (plus the <code>-p</code> for port forwarding as before). You don't want to use the entire <code>/platform/web-application</code> folder as your volume (the <code>node_modules</code> folder in it and some other files/folder should not be synched, only the code you've written), so it might be a good idea to put your source code files in a new folder, for example called <code>/platform/web-application/src</code>, and use volumes to synch only this subfolder instead.</p>
<p>However, synching files is not enough to keep your web application in the container up to date. Each time a JavaScript file changes the web application running in the container needs to be restarted. To make that happen we can use an npm package called <a href="https://nodemon.io/" target="_blank" rel="noopener noreferrer">nodemon<OutboundLink/></a>. Simply:</p>
<ol>
<li>Install <code>nodemon</code> by running the command <code>npm install nodemon</code> in <code>/platform/web-application</code>.</li>
</ol>
<div class="custom-container warning"><p class="custom-container-title">Note</p>
<p>Since <code>nodemon</code> is only needed during development, and not when the application runs for real on a server, you should really add this package in the <code>devDependencies</code> list in <code>package.json</code>, instead of the ordinary <code>dependencies</code> section.</p>
<p>Read mor about this in the docs <a href="https://docs.npmjs.com/specifying-dependencies-and-devdependencies-in-a-package-json-file" target="_blank" rel="noopener noreferrer">Specifying dependencies and devDependencies in a package.json file<OutboundLink/></a>.</p>
</div>
<ol start="2">
<li>Change <code>package.json</code> to contain a script called <code>start</code> that executes
<code>nodemon src/app.js -L</code>.</li>
</ol>
<div class="custom-container warning"><p class="custom-container-title">Volumes on Windows</p>
<p>If you have put your source code on the file system for the Linux distribution in Windows Subsystem for Linux, then you can skip the <code>-L</code> flag. That flag activates a workaround causing the computer to do more work than it should need to, but the flag is required if you put your source code on the Windows file system. To learn why, read <a href="https://docs.docker.com/docker-for-windows/wsl/#best-practices" target="_blank" rel="noopener noreferrer">Docker's best practices<OutboundLink/></a> and <a href="https://github.com/remy/nodemon#application-isnt-restarting" target="_blank" rel="noopener noreferrer">nodemon's docs<OutboundLink/></a>.</p>
<p>Docker has a setting called <code>Use the WSL 2 based engine</code>. For Windows 10 Home users, this option must be checked. For Windows 10 Pro users, this option can be unchecked, in which case volumes will work a little bit different, and you don't don't need to use <code>-L</code> flag if your source code is placed on Windows file system. However, you will still get overall better performance by leaving this option checked and place your source code on the file system for the Linux distribution you are running on Windows Subsystem for Linux (according to Docker).</p>
</div>
<ol start="3">
<li>Change your <code>Dockerfile</code> to start the web application with the command <code>npm run start</code>.</li>
</ol>
<p>When you have built and run this new image and used volumes to synch folders the web application in the container should restart as soon as you change any JavaScript file the web application is dependent on. Your new workflow will simply be:</p>
<ol>
<li>Build and run the image using volumes.</li>
<li>Make changes to JavaScript code on your computer
(Docker will automatically synch the files to the container, and <code>nodemon</code> in the container will restart the web application when this happens).</li>
<li>See the changes in your web browser.</li>
<li>Restart on #2.</li>
</ol>
<div class="custom-container warning"><p class="custom-container-title">Installing new npm packages</p>
<p>When you install a new npm package you need to restart on #1 above. The container will only read the <code>dependencies</code> list in <code>package.json</code> and download the npm packages to the <code>node_modules</code> folder when you build the image.</p>
</div>
<p>If you want to be able to debug your Node.js application from Visual Studio Code, you need to tell Visual Studio Code how to connect to the Node.js application running in the container. The tutorial <a href="https://blog.theodo.com/2018/10/setting-debugger-node-js-docker-vs-code/" target="_blank" rel="noopener noreferrer">Setting up Debugger for Node.js in Docker with VS code<OutboundLink/></a> shows you how you can do that. That tutorial makes use of Docker Compose, which you will start using after you have created a database image to run your database.</p>
<p>You have now successfully setup a project suitable for implementing a web application in Node.js with Docker containers. Good job!</p>
<h3 id="the-database-image" tabindex="-1"><a class="header-anchor" href="#the-database-image" aria-hidden="true">#</a> The Database image</h3>
<p>Since the MySQL database should run in a separate container, let us do something similar for the database as we did for the web application.</p>
<p>Start by creating a new folder inside <code>/platform</code> to store information about the database image. In this document we will refer to that sub-folder as <code>/platform/database</code>. Create a new file in this sub-folder called <code>Dockerfile</code> with the content similar to the one in <FigureNumber /> below. That Dockerfile describes an image inheriting from <code>mysql:5.7.24</code>, which is an image that runs a MySQL server. Read more about <a href="https://hub.docker.com/_/mysql" target="_blank" rel="noopener noreferrer">the MySQL image<OutboundLink/></a> on Docker Hub.</p>
<Figure caption="The content of Dockerfile in the database folder.">
<div class="language-text ext-text line-numbers-mode"><pre v-pre class="language-text"><code>FROM mysql:5.7.24

COPY initialize-database.sql /docker-entrypoint-initdb.d/
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div></Figure>
<p>Then create a new file in <code>/platform/database</code> called <code>initialize-database.sql</code>. The SQL code you write in that file will be executed by the MySQL server when it starts, so in this file you can write SQL code that creates the tables in the database your platform needs.</p>
<p>The database image can be started and stopped in the same way as your web application image. After you have started it you should be able to connect to it from your host computer using <a href="http://localhost:3306" target="_blank" rel="noopener noreferrer">http://localhost:3306<OutboundLink/></a> (or the Docker Machine's IP address). If you have a MySQL Database Management Tool (such as <a href="https://www.mysql.com/products/workbench/" target="_blank" rel="noopener noreferrer">MySQL Workbench<OutboundLink/></a> or one of the <a href="https://marketplace.visualstudio.com/search?term=mysql&amp;target=VSCode" target="_blank" rel="noopener noreferrer">MySQL extensions to Visual Studio Code<OutboundLink/></a>), try it out!</p>
<h3 id="using-docker-compose" tabindex="-1"><a class="header-anchor" href="#using-docker-compose" aria-hidden="true">#</a> Using Docker Compose</h3>
<p>Docker has a tool called <a href="https://docs.docker.com/compose/" target="_blank" rel="noopener noreferrer">Docker Compose<OutboundLink/></a> that greatly simplifies for us to start and stop multiple containers at the same time, as well as handle the connection between them (which we need, since the web application needs to communicate with the database). To use it, simply create a file in <code>/platform</code> called <code>docker-compose.yml</code> with the content shown in <FigureNumber /> below.</p>
<Figure caption="The content of docker-compose.yml in /platform.">
<div class="language-text ext-text line-numbers-mode"><pre v-pre class="language-text"><code>version: '3'
services:
  "web-application":
    build: "./web-application"
    ports:
      - "3000:8080"
      - "9229:9229"
    volumes:
      - "./web-application/src:/web-application/src"
    depends_on:
      - database
  database:
    build: ./database
    ports:
      - "3306:3306"
    environment:
      - MYSQL_ROOT_PASSWORD=theRootPassword
      - MYSQL_DATABASE=webAppDatabase
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br></div></div></Figure>
<p>Your images can now be built and started in containers using the <code>docker-compose up</code> command in <code>/platform</code>, and the web application can now use the domain name <code>database</code> to connect to the MySQL database that runs in another container.</p>
<div class="custom-container tip"><p class="custom-container-title">Rebuilding images</p>
<p><code>docker-compose up</code> will run the latest build of your images. If you make changes to an image, you need to rebuild it before using <code>docker-compose up</code>, or simply use the command <code>docker-compose up --build</code>.</p>
</div>
<div class="custom-container warning"><p class="custom-container-title">Handling anonymous volumes</p>
<p>The MySQL image uses an anonymous volume to store it's data, and by default it's only created the first time you start it using <code>docker-compose up</code>. A result of this is that your SQL code in the <code>/docker-entrypoint-initdb.d/</code> in the image will only be executed the first time you start it with <code>docker-compose up</code>. The next time you run <code>docker-compose up</code> it will resume using the tables from before and not execute your SQL code in <code>/docker-entrypoint-initdb.d/</code>. This is usually NOT what you want. You can get around this by using the command <code>docker-compose down</code> before you run <code>docker-compose up</code>, or you can add the flag <code>--renew-anon-volumes</code> (short form is <code>-V</code>) to <code>docker-compose up</code>.</p>
<p>For more information, see <a href="https://github.com/docker/compose/issues/2127" target="_blank" rel="noopener noreferrer">Issue #2127 in the Docker Compose repository<OutboundLink/></a>.</p>
</div>
<h2 id="part-5-implementing-the-web-application" tabindex="-1"><a class="header-anchor" href="#part-5-implementing-the-web-application" aria-hidden="true">#</a> Part 5: Implementing the Web Application</h2>
<p>Before you start working on this part, you are recommended to:</p>
<ul>
<li>Read/View the following lectures:
<ul>
<li><RouterLink to="/lectures/layered-architecture-in-node-js/">Layered Architecture in Node.js</RouterLink></li>
<li><RouterLink to="/lectures/asynchronous-programming-in-js/">Asynchronous Programming in JavaScript</RouterLink></li>
</ul>
</li>
</ul>
<hr>
<p>Implement the website using a three-layered architecture:</p>
<ul>
<li><strong>The Presentation Layer</strong> should only receive HTTP requests and generate and send back HTTP responses. This is the only layer that should use HTTP, Sessions, Cookies, HTML and CSS (and client-side JavaScript, if you want), so it is only in this layer we will be using Express. It will carry out the HTTP request by telling the Business Logic Layer what to do.</li>
<li><strong>The Business Logic Layer</strong> should only do computations. This includes applying business logic rules such as validation as authorization. If the Business Logic Layer needs to store/retrieve/update/delete any resource, it will simply ask the Data Access Layer to do that for it.</li>
<li><strong>The Data Access Layer</strong> should only be responsible for persistence of the resources, in this case stored in a MySQL database. It should provide operations one can use to store/retrieve/update/delete resources in the database. This is the only layer where you should use SQL.</li>
</ul>
<FigureNumber /> below is a visualization of a three-layered architecture.
<Figure caption="A three-layered architecture. The Presentation Layer is the one receiving HTTP requests from clients, to carry them out is uses the Business Logic Layer, and so on.">
<RenderMermaid graph-definition="
graph LR
	wb[Web Browser]
	subgraph Web Application
		pl(Presentation Layer)
		bll(Business Logic Layer)
		dal(Data Access Layer)
	end
	db[Database]
	wb --HTTP--> pl
	pl --> bll
	bll --> dal
	dal --SQL--> db
" />
</Figure>
<p>There exists different ways one can separate the code into these three layers. The simplest way is probably to just create three different folders, one for each layer, and then put all code belonging to a layer in the corresponding folder.</p>
<p>To help you get started you can use the skeleton code available in the ZIP file <a href="files/the-community.zip">the-community.zip</a>.</p>
<p>On your platform, users should at least be able to create new resources and to browse and view existing resources. You do not need to implement update and delete functionality; that is an extra task you can complete at the end of the course if you have time for it. You neither need to implement authorization/login functionality now. That is the next part of the project work.</p>
<h2 id="part-6-adding-authorization" tabindex="-1"><a class="header-anchor" href="#part-6-adding-authorization" aria-hidden="true">#</a> Part 6: Adding Authorization</h2>
<p>Before you start working on this part, you are recommended to:</p>
<ul>
<li>Read/View the following lectures:
<ul>
<li><RouterLink to="/lectures/handling-concurrent-http-requests/">Handling Concurrent HTTP Requests</RouterLink></li>
<li><RouterLink to="/lectures/scaling-web-applications/">Scaling Web Applications</RouterLink></li>
<li><RouterLink to="/lectures/scaling-databases/">Scaling Databases</RouterLink></li>
</ul>
</li>
</ul>
<hr>
<p>When the web application receives a successful sign in request from a client it needs to remember to which account that client signed in to, so it knows that when it receives requests from that client in the future. To do that one usually uses sessions. A session contains information about a client stored on the server-side. Where the sessions are stored on the server-side has a major impact on how the web application can be scaled.</p>
<p>The most common used approach to scaling web applications is by running multiple instances of them and use a load balancer to distribute the load (the HTTP requests) between them, as seen in <FigureNumber /> below. Each instance can for example run in its own Docker container or on its own server. When the load increases, we just launch more instances to handle it, and when the load decreases, we just remove some of the instances (so we don't need to pay for having servers up and running that don't do any work).</p>
<Figure caption="A common used architecture for scaling. The Load Balancer receives HTTP requests from Clients and then forwards them to (one of) the Web Application Instances.">
<RenderMermaid graph-definition="
graph LR
	clients[Clients]
	lb[Load Balancer]
	wai1[Web Application Instance 1]
	wai2[Web Application Instance 2]
	wain[Web Application Instance n]
	db[Database]
	clients --HTTP--> lb
	lb --HTTP--> wai1 --SQL-->db
	lb --HTTP--> wai2 --SQL-->db
	lb --HTTP--> wain --SQL-->db
" />
</Figure>
<p>With this architecture, if the sessions are stored locally in the web application instances, then each clients' HTTP request must be forwarded to the same instance all the time, because it is only that instance that have access to the client's session. This can be achieved with a technique called sticky sessions (the load balancer tells the client to create a cookie specifying which web application instance it should forward the requests from that particular client to), but a solution that is easier to implement is to simply store the sessions in the database. This works since all web application instances are connected to the same database, so they store the sessions at the same place. However, fetching the session from the database for each request will make it take a little bit longer to carry out the requests, so this solution is not necessarily the best one.</p>
<p>The sessions could be stored in the MySQL database we already have, but sessions are just temporary data. They don't really have anything to do with the data we store in our MySQL database, so why use the same database? According to the single responsibility principle it is much better to store it in another database, ones whose only responsibility it to store sessions. Since sessions don't consist of any relational data, it is better to use a NoSQL database instead of a relational database since NoSQL databases often are faster and easier to scale than relational databases. The type of NoSQL database that makes most sense for this use-case is a key-value database (the session id is used as the key, and the session itself as the value).</p>
<p>Add another container to your solution that runs a Redis database, which is a key-value database. Then connect to it from your web application and store your sessions in it. In your web application you will:</p>
<ul>
<li>Use the npm package <a href="https://github.com/expressjs/session" target="_blank" rel="noopener noreferrer">express-session<OutboundLink/></a> to add session support to Express.</li>
<li>Use the npm package <a href="https://github.com/tj/connect-redis" target="_blank" rel="noopener noreferrer">connect-redis<OutboundLink/></a> to store the sessions in a Redis database.</li>
</ul>
<p>Then add the login feature to your web application and add authorization. Remember that sessions are something we use to make HTTP stateful (hence we only use them in the presentation layer) and authorization is something we should implement in the business logic layer (which users that are authorized to do what should be independent of how they use our application).</p>
<h2 id="part-7-using-dependency-injection" tabindex="-1"><a class="header-anchor" href="#part-7-using-dependency-injection" aria-hidden="true">#</a> Part 7: Using Dependency Injection</h2>
<p>Before you start working on this part, you are recommended to:</p>
<ul>
<li>Read/View the following lectures:
<ul>
<li><RouterLink to="/lectures/dependency-injection-in-node-js/">Dependency Injection in Node.js</RouterLink></li>
</ul>
</li>
</ul>
<hr>
<p>The three layers the web application consists of have some dependencies:</p>
<ul>
<li>The <code>Presentation Layer</code> has some dependencies on the <code>Business Logic Layer</code>.</li>
<li>The <code>Business Logic Layer</code> has some dependencies on the <code>Data Access Layer</code>.</li>
</ul>
<p>These dependencies are hardcoded at multiple places, as shown in the tabs below.</p>
<CodeGroup>
<CodeGroupItem title="Presentation Layer">
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token comment">// account-router.js</span>

<span class="token keyword">const</span> express <span class="token operator">=</span> <span class="token function">require</span><span class="token punctuation">(</span><span class="token string">'express'</span><span class="token punctuation">)</span>
<span class="token comment">// Here we have hardcoded a dependency on the account manager.</span>
<span class="token keyword">const</span> accountManager <span class="token operator">=</span> <span class="token function">require</span><span class="token punctuation">(</span><span class="token string">'../business-logic-layer/account-manager'</span><span class="token punctuation">)</span>

<span class="token keyword">const</span> router <span class="token operator">=</span> express<span class="token punctuation">.</span><span class="token function">Router</span><span class="token punctuation">(</span><span class="token punctuation">)</span>

router<span class="token punctuation">.</span><span class="token function">get</span><span class="token punctuation">(</span><span class="token string">"/"</span><span class="token punctuation">,</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token parameter">request<span class="token punctuation">,</span> response</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
  accountManager<span class="token punctuation">.</span><span class="token function">getAllAccount</span><span class="token punctuation">(</span><span class="token keyword">function</span><span class="token punctuation">(</span><span class="token parameter">errors<span class="token punctuation">,</span> accounts</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    <span class="token keyword">const</span> model <span class="token operator">=</span> <span class="token punctuation">{</span>
      errors<span class="token operator">:</span> errors<span class="token punctuation">,</span>
      accounts<span class="token operator">:</span> accounts
    <span class="token punctuation">}</span>
    response<span class="token punctuation">.</span><span class="token function">render</span><span class="token punctuation">(</span><span class="token string">"accounts-list-all.hbs"</span><span class="token punctuation">,</span> model<span class="token punctuation">)</span>
  <span class="token punctuation">}</span><span class="token punctuation">)</span>
<span class="token punctuation">}</span><span class="token punctuation">)</span>

module<span class="token punctuation">.</span>exports <span class="token operator">=</span> router
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br></div></div></CodeGroupItem>
<CodeGroupItem title="Business Logic Layer">
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token comment">// account-manager.js</span>

<span class="token comment">// Here we have hardcoded a dependency on the account repository.</span>
<span class="token keyword">const</span> accountRepository <span class="token operator">=</span> <span class="token function">require</span><span class="token punctuation">(</span><span class="token string">'../data-access-layer/account-repository'</span><span class="token punctuation">)</span>

exports<span class="token punctuation">.</span><span class="token function-variable function">getAllAccounts</span> <span class="token operator">=</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token parameter">callback</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
  accountRepository<span class="token punctuation">.</span><span class="token function">getAllAccounts</span><span class="token punctuation">(</span><span class="token keyword">function</span><span class="token punctuation">(</span><span class="token parameter">errors<span class="token punctuation">,</span> accounts</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    <span class="token function">callback</span><span class="token punctuation">(</span>errors<span class="token punctuation">,</span> accounts<span class="token punctuation">)</span>
  <span class="token punctuation">}</span><span class="token punctuation">)</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br></div></div></CodeGroupItem>
<CodeGroupItem title="Data Access Layer">
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token comment">// account-repository.js</span>

<span class="token comment">// No dependency on another layer, but shown for completeness.</span>
<span class="token keyword">const</span> mysql <span class="token operator">=</span> <span class="token function">require</span><span class="token punctuation">(</span><span class="token string">'mysql'</span><span class="token punctuation">)</span>
<span class="token keyword">const</span> connection <span class="token operator">=</span> mysql<span class="token punctuation">.</span><span class="token function">connect</span><span class="token punctuation">(</span><span class="token operator">...</span><span class="token punctuation">)</span>

exports<span class="token punctuation">.</span><span class="token function-variable function">getAllAccounts</span> <span class="token operator">=</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token parameter">callback</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
  <span class="token keyword">const</span> query <span class="token operator">=</span> <span class="token string">"SELECT * FROM accounts ORDER BY username"</span>
  connection<span class="token punctuation">.</span><span class="token function">query</span><span class="token punctuation">(</span>query<span class="token punctuation">,</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token parameter">error<span class="token punctuation">,</span> accounts</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    <span class="token keyword">if</span><span class="token punctuation">(</span>error<span class="token punctuation">)</span><span class="token punctuation">{</span>
      <span class="token function">callback</span><span class="token punctuation">(</span><span class="token punctuation">[</span><span class="token string">"Database error"</span><span class="token punctuation">]</span><span class="token punctuation">,</span> <span class="token keyword">null</span><span class="token punctuation">)</span>
    <span class="token punctuation">}</span><span class="token keyword">else</span><span class="token punctuation">{</span>
      <span class="token function">callback</span><span class="token punctuation">(</span><span class="token punctuation">[</span><span class="token punctuation">]</span><span class="token punctuation">,</span> accounts<span class="token punctuation">)</span>
    <span class="token punctuation">}</span>
  <span class="token punctuation">}</span><span class="token punctuation">)</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br></div></div></CodeGroupItem>
</CodeGroup>
<p>Hardcoding dependencies like this makes our web application less flexible. For example, each time we use the account manager, the account manager will always use the account repository that works with the data in the MySQL databases. You might think this is not such a big deal, because that's what we want to happen when our web application runs, right? Yes, if we want to use the code to run the web application, then yes, that is what we want to happen. But what if we write tests and just want to run the code inside the account manager to test if that works? Then we don't necessarily want the account manager to make use of the account repository that communicates with the MySQL database, because if the test fails when we run it, then the problem could just as well be in the account repository (e.g. broken connection to the database).</p>
<p>When we test the code in the account manager using tests we have written, we don't necessarily want it to use the account repository that makes use of the MySQL database. Instead, we can provide a mockup of the account repository, as the one shown in <FigureNumber /> below.</p>
<Figure caption="Mockup of the account repository in the Data Access Layer.">
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">const</span> allAccounts <span class="token operator">=</span> <span class="token punctuation">[</span><span class="token punctuation">]</span> <span class="token comment">// All accounts are stored in this array.</span>

exports<span class="token punctuation">.</span><span class="token function-variable function">getAllAccounts</span> <span class="token operator">=</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token parameter">callback</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
  <span class="token function">callback</span><span class="token punctuation">(</span><span class="token punctuation">[</span><span class="token punctuation">]</span><span class="token punctuation">,</span> allAccounts<span class="token punctuation">)</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div></Figure>
<p>For our tests, we want the account manager to make use of the mockup version of the account repository, and when we run the web application for real we want the account manager to make use of the account repository that makes use of the MySQL database. Because of this, we can't hardcode the dependencies we have.</p>
<div class="custom-container tip"><p class="custom-container-title">Mockups not only for testing</p>
<p>Mockups are not necessarily only created for our own code when doing testing. Another example is making use of third-party services through APIs that costs money. When our web application runs in production for real, we want to make use of the real third-party API and are willing to pay for using it, but when we run it locally on our own computers for development/testing, we don't want to pay for using it, so we use a mockup version instead.</p>
</div>
<p>So, in our account manager, we can't hardcode which version of the account repository we should use. Instead, the account repository that should be used should be injected to the account manager. The account manager itself doesn't care about which account repository it uses as long as it has access to the one it should be using. We say that the account manager is dependent on the interface of the account repository, which means that all different account repositories we implement must expose the same set of functions/same interface the account manager can use.</p>
<p>When we start the program, we specify which account repository to use, and then when we use the account manager it will use the account repository we specified. How we make all this happen depends on which dependency injection framework/container we use. The npm package <a href="https://github.com/jeffijoe/awilix" target="_blank" rel="noopener noreferrer">awilix<OutboundLink/></a> gives us this functionality. The figures below shows one way to use it.</p>
<CodeGroup>
<CodeGroupItem title="Data Access Layer">
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token comment">// account-repository.js</span>

module<span class="token punctuation">.</span><span class="token function-variable function">exports</span> <span class="token operator">=</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token parameter"><span class="token punctuation">{</span><span class="token punctuation">}</span></span><span class="token punctuation">)</span><span class="token punctuation">{</span>
  <span class="token comment">// Name all the dependencies in the curly brackets above (none in this case). </span>
  
  <span class="token keyword">const</span> allAccounts <span class="token operator">=</span> <span class="token punctuation">[</span><span class="token punctuation">]</span>
  
  <span class="token keyword">return</span> <span class="token punctuation">{</span>
    <span class="token function-variable function">getAllAccounts</span><span class="token operator">:</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token parameter">callback</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
      <span class="token function">callback</span><span class="token punctuation">(</span><span class="token punctuation">[</span><span class="token punctuation">]</span><span class="token punctuation">,</span> allAccounts<span class="token punctuation">)</span>
    <span class="token punctuation">}</span>
    <span class="token comment">// Continue to list all other functions in account repository here.</span>
  <span class="token punctuation">}</span>
  
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br></div></div></CodeGroupItem>
<CodeGroupItem title="Business Logic Layer">
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token comment">// account-manager.js</span>

module<span class="token punctuation">.</span><span class="token function-variable function">exports</span> <span class="token operator">=</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token parameter"><span class="token punctuation">{</span>accountRepository<span class="token punctuation">}</span></span><span class="token punctuation">)</span><span class="token punctuation">{</span>
  <span class="token comment">// Name all the dependencies in the curly brackets above.</span>
  
  <span class="token keyword">return</span> <span class="token punctuation">{</span>
    <span class="token function-variable function">getAllAccounts</span><span class="token operator">:</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token parameter">callback</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
      accountRepository<span class="token punctuation">.</span><span class="token function">getAllAccounts</span><span class="token punctuation">(</span><span class="token keyword">function</span><span class="token punctuation">(</span><span class="token parameter">errors<span class="token punctuation">,</span> accounts</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
        <span class="token function">callback</span><span class="token punctuation">(</span>errors<span class="token punctuation">,</span> accounts<span class="token punctuation">)</span>
      <span class="token punctuation">}</span><span class="token punctuation">)</span>
    <span class="token punctuation">}</span>
    <span class="token comment">// Continue to list all other functions in account manager here.</span>
  <span class="token punctuation">}</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br></div></div></CodeGroupItem>
<CodeGroupItem title="Presentation Layer">
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token comment">// account-router.js</span>

<span class="token keyword">const</span> express <span class="token operator">=</span> <span class="token function">require</span><span class="token punctuation">(</span><span class="token string">'express'</span><span class="token punctuation">)</span>

module<span class="token punctuation">.</span><span class="token function-variable function">exports</span> <span class="token operator">=</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token parameter"><span class="token punctuation">{</span>accountManager<span class="token punctuation">}</span></span><span class="token punctuation">)</span><span class="token punctuation">{</span>
  <span class="token comment">// Name all the dependencies in the curly brackets above.</span>
  
  <span class="token keyword">const</span> router <span class="token operator">=</span> express<span class="token punctuation">.</span><span class="token function">Router</span><span class="token punctuation">(</span><span class="token punctuation">)</span>
  
  router<span class="token punctuation">.</span><span class="token function">get</span><span class="token punctuation">(</span><span class="token string">"/"</span><span class="token punctuation">,</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token parameter">request<span class="token punctuation">,</span> response</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    accountManager<span class="token punctuation">.</span><span class="token function">getAllAccount</span><span class="token punctuation">(</span><span class="token keyword">function</span><span class="token punctuation">(</span><span class="token parameter">errors<span class="token punctuation">,</span> accounts</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
      <span class="token keyword">const</span> model <span class="token operator">=</span> <span class="token punctuation">{</span>
        errors<span class="token operator">:</span> errors<span class="token punctuation">,</span>
        accounts<span class="token operator">:</span> accounts
      <span class="token punctuation">}</span>
      response<span class="token punctuation">.</span><span class="token function">render</span><span class="token punctuation">(</span><span class="token string">"accounts-list-all.hbs"</span><span class="token punctuation">,</span> model<span class="token punctuation">)</span>
    <span class="token punctuation">}</span><span class="token punctuation">)</span>
  <span class="token punctuation">}</span><span class="token punctuation">)</span>
  
  <span class="token keyword">return</span> router
  
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br><span class="line-number">21</span><br><span class="line-number">22</span><br></div></div></CodeGroupItem>
</CodeGroup>
<p>With Awilix we can then in the main file specify which dependencies we want to use, as shown in <FigureNumber /> below.  If we want to change which account repository to use, we just need to change that at line 4 in that file.</p>
<Figure caption="Creating an using an awilix container to resolve dependencies.">
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token keyword">const</span> awilix <span class="token operator">=</span> <span class="token function">require</span><span class="token punctuation">(</span><span class="token string">'awilix'</span><span class="token punctuation">)</span>

<span class="token comment">// Import the ones we want to use (real or mockup), real in this case.</span>
<span class="token keyword">const</span> accountRepository <span class="token operator">=</span> <span class="token function">require</span><span class="token punctuation">(</span><span class="token string">'data-access-layer/account-repository'</span><span class="token punctuation">)</span>
<span class="token keyword">const</span> accountManager <span class="token operator">=</span> <span class="token function">require</span><span class="token punctuation">(</span><span class="token string">'business-logic-layer/account-manager'</span><span class="token punctuation">)</span>
<span class="token keyword">const</span> accountRouter <span class="token operator">=</span> <span class="token function">require</span><span class="token punctuation">(</span><span class="token string">'presentation-layer/account-router'</span><span class="token punctuation">)</span>

<span class="token comment">// Create a container and add the dependencies we want to use.</span>
<span class="token keyword">const</span> container <span class="token operator">=</span> awilix<span class="token punctuation">.</span><span class="token function">createContainer</span><span class="token punctuation">(</span><span class="token punctuation">)</span>
container<span class="token punctuation">.</span><span class="token function">register</span><span class="token punctuation">(</span><span class="token string">"accountRepository"</span><span class="token punctuation">,</span> awilix<span class="token punctuation">.</span><span class="token function">asFunction</span><span class="token punctuation">(</span>accountRepository<span class="token punctuation">)</span><span class="token punctuation">)</span>
container<span class="token punctuation">.</span><span class="token function">register</span><span class="token punctuation">(</span><span class="token string">"accountManager"</span><span class="token punctuation">,</span> awilix<span class="token punctuation">.</span><span class="token function">asFunction</span><span class="token punctuation">(</span>accountManager<span class="token punctuation">)</span><span class="token punctuation">)</span>
container<span class="token punctuation">.</span><span class="token function">register</span><span class="token punctuation">(</span><span class="token string">"accountRouter"</span><span class="token punctuation">,</span> awilix<span class="token punctuation">.</span><span class="token function">asFunction</span><span class="token punctuation">(</span>accountRouter<span class="token punctuation">)</span><span class="token punctuation">)</span>

<span class="token comment">// Retrieve the router, which resolves all other dependencies.</span>
<span class="token keyword">const</span> theAccountRouter <span class="token operator">=</span> container<span class="token punctuation">.</span><span class="token function">resolve</span><span class="token punctuation">(</span><span class="token string">"accountRouter"</span><span class="token punctuation">)</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br></div></div></Figure>
<p>So, to use dependency injection with Awilix like this you need to:</p>
<ol>
<li>Change your JavaScript files to export a single function that receives an object (the container) containing all the dependencies that should be used. <br>
Note: Awilix also supports dependency injection using classes, so feel free to use the if you prefer.</li>
<li>In your main file, create a new Awilix container and add all these functions to it.</li>
<li>Retrieve the values you need from the container (most likely only the Express <code>app</code> value).</li>
</ol>
<p>If you want, feel free to use any other dependency injection method you prefer, but you can't expect to get any help from a teacher using it.</p>
<h2 id="part-8-multiple-data-access-layers-orm" tabindex="-1"><a class="header-anchor" href="#part-8-multiple-data-access-layers-orm" aria-hidden="true">#</a> Part 8: Multiple Data Access Layers/ORM</h2>
<p>Before you start working on this part, you are recommended to:</p>
<ul>
<li>Read/View the following lectures:
<ul>
<li><RouterLink to="/lectures/using-sequelize-in-node-js/">Using Sequelize in Node.js</RouterLink></li>
</ul>
</li>
</ul>
<hr>
<p>Now that your web application makes use of dependency injection, let us see if it works by implementing another data access layer. The new data access layer should have the same interface as the old one (must implement the same exposed functions, must send back identical errors, etc.) but should be implemented differently. Let us implement it using an Object-Relational Mapping framework, so you get some practice on using that.</p>
<p>Use whichever ORM you want, but <a href="https://sequelize.org/" target="_blank" rel="noopener noreferrer">Sequelize<OutboundLink/></a> is quite simple and straight forward to use. Feel free to use any database you want. Using your existing MySQL database is OK, but to practice on using Docker we recommend you add another container running another database, e.g. PostgreSQL.</p>
<p>When you're done your architecture could look like the one shown in <FigureNumber /> below, but when running the application only one of the Data Access Layers will be used.</p>
<Figure caption="Current architecture.">
<RenderMermaid graph-definition="
graph LR
	wb[Web Browser]
	subgraph Web Application
		pl(Presentation Layer)
		bll(Business Logic Layer)
		dalMy(Data Access Layer MySQL)
		dalPost(Data Access Layer PostgreSQL)
	end
	dbMy[Database MySQL]
	dbPost[Database PostgreSQL]
	wb --HTTP--> pl
	pl --> bll
	bll --> dalMy --SQL--> dbMy
	bll --> dalPost --SQL--> dbPost
" />
</Figure>
<h2 id="part-9-adding-a-rest-api" tabindex="-1"><a class="header-anchor" href="#part-9-adding-a-rest-api" aria-hidden="true">#</a> Part 9: Adding a REST API</h2>
<p>Before you start working on this part, you are recommended to:</p>
<ul>
<li>Read/View the following lectures:
<ul>
<li><RouterLink to="/lectures/rest-api-basics/">REST API Basics</RouterLink></li>
<li><RouterLink to="/lectures/rest-api-in-express/">REST API in Express</RouterLink></li>
<li><RouterLink to="/lectures/rest-api-authorization/">REST API Authorization</RouterLink></li>
<li><RouterLink to="/lectures/json-web-tokens/">JSON Web Tokens</RouterLink></li>
<li><RouterLink to="/lectures/third-party-authentication/">Third-Party Authentication</RouterLink></li>
</ul>
</li>
</ul>
<hr>
<p>Users can use the platform on their smartphones through the web browser on it, but the user experience can often be improved my implementing and let the users use a native smartphone application instead. This way, the GUI would consist of GUI components native applications use, which appear much more appealing to the user compared to the GUI components webpages consists of. Furthermore, a native application would also be able to make use of native features on the smartphone which aren't accessible through client-side JavaScript code.</p>
<p>However, unlike the web application, a native smartphone application can't communicate directly with our database. One need to know the username and password to the database to use it, and we can't put that in a native smartphone application, because then any hacker would be able to retrieve it from the smartphone application after they have downloaded it. Then they could login to the database and change it however they please. Instead we will add a REST API to our web application, and the native application should be able to store/retrieve/update/delete data in the database through it. The platform will be as shown in <FigureNumber /> below.</p>
<Figure caption="Overview of the platform.">
<RenderMermaid graph-definition="
graph LR
	wb[Web Browser]
	nsa[Native Smartphone Application]
	wa[Web Application]
	db[Database]
	wb --HTTP HTML/x-www-form-urlencoded--> wa
	nsa --HTTP REST API JSON-->wa
	wa --SQL-->db
" />
</Figure>
<p>The difference between the way the web browser and the native smartphone application communicates with the web application will primarily be the data format they use. Both will communicate with the web application using HTTP, but the web browser will send data (submitted forms) in the format <code>application/x-www-form-urlencoded</code> and receive back HTML code, while the native smartphone application will send and receive in the format <code>application/json</code> (JSON), which is a much simpler data format. How to remember which account the user has logged in to will also differ.</p>
<p>The REST API can be implemented as an additional Presentation Layer in the web application, as shown in <FigureNumber /> below. Since the users who use the platform through the native smartphone application should (theoretically) be able to do the same thing as users who use the platform through the web browser, we should not need to make that much changes to the code in the Business Logic Layer nor the Data Access Layer (but we might need to add code). You should be able to re-use most of the functionalities from these layers.</p>
<Figure caption="Current architecture (with one Data Access Layer).">
<RenderMermaid graph-definition="
graph LR
	wb[Web Browser]
	nsa[Native Smartphone Application]
	subgraph Web Application
		plw(Presentation Layer Website)
		plr(Presentation Layer REST API)
		bll(Business Logic Layer)
		dal(Data Access Layer)
	end
	db[Database]
	wb --HTTP HTML/x-www-form-urlencoded--> plw --> bll
	nsa --HTTP REST API JSON--> plr --> bll
	bll --> dal --SQL--> db
" />
</Figure>
<p>Now the REST API can run as its own Express application in one container, and the Website runs as an Express application in its own container, and these can scale independently of each other. If you prefer, you can run both Presentation Layers in the same container. Then each Presentation Layer can export it's own Express Application object which then is used by a fourth layer (which also is an Express application). See the documentation for <a href="https://expressjs.com/en/4x/api.html#app.use" target="_blank" rel="noopener noreferrer">app.use()<OutboundLink/></a> to learn how one Express Application object can make use of other Express Application objects.</p>
<p>The REST API needs to support the following operations:</p>
<ul>
<li>Create a new account</li>
<li>Login to an existing account</li>
<li>When logged in, apply CRUD (Create, Read, Update, Delete) operations on at least one of the other two type of resources that belongs to the account the user logged in to</li>
</ul>
<p>One usually doesn't use sessions in REST APIs to remember that a user has logged in. Sessions relies on cookies and supporting all features of cookies is hard for clients. Instead, one usually uses tokens instead, which are much simpler.</p>
<p>Tokens can be implemented in different ways, but the simplest way is by using JSON Web Tokens. These can easily be created and verified in Node.js using the npm package <a href="https://www.npmjs.com/package/jsonwebtoken" target="_blank" rel="noopener noreferrer">jsonwebtoken<OutboundLink/></a>.</p>
<p>Implement Access Tokens as described by the <a href="https://oauth.net/2/" target="_blank" rel="noopener noreferrer">OAuth 2.0 Framework<OutboundLink/></a> and implement ID Tokens as described by the <a href="https://openid.net/connect/" target="_blank" rel="noopener noreferrer">OpenID Connect<OutboundLink/></a> specification. Use the <code>Resource Owner Password Credentials Grant</code>, and make sure to get all the details right (status code, bodies, headers, tokens, etc. should be as described in the specifications).</p>
<h2 id="part-10-implementing-a-single-page-application" tabindex="-1"><a class="header-anchor" href="#part-10-implementing-a-single-page-application" aria-hidden="true">#</a> Part 10: Implementing a Single-Page Application</h2>
<p>Before you start working on this part, you are recommended to:</p>
<ul>
<li>Read/View the following lectures:
<ul>
<li><RouterLink to="/lectures/content-delivery-networks/">Content Delivery Networks</RouterLink></li>
<li><RouterLink to="/lectures/ajax/">AJAX</RouterLink></li>
<li><RouterLink to="/lectures/sop-and-cors/">Same-Origin Policy and Cross-Origin Resource Sharing</RouterLink></li>
</ul>
</li>
</ul>
<hr>
<p>Since implementing a native smartphone application is not part of this course, you will instead implement a Single-Page Application (SPA). A SPA is a website that only consists of a single webpage and a lot of client-side JavaScript code to make it appear to work as an ordinary application. When users click on links or submit forms, the web browser doesn't submit these and load a new web page. Instead, client-side JavaScript codes listens for these events and handles them itself. This way, we can add loading indicators while fetching a new &quot;page&quot;/submitting a form and add transition animations when going from one &quot;page&quot; to another, etc. to improve the user experience.</p>
<p>So, your next task is to implement a SPA that makes use of the REST API on the platform. In the SPA users should be able to:</p>
<ul>
<li>Create a new account.</li>
<li>Login to an existing account, and then:
<ul>
<li>Apply CRUD operation for at least one of the other two type of resources.</li>
</ul>
</li>
</ul>
<p>Since the SPA only consists of static HTML, CSS and JS files (and possibly images), we don't need to implement a fancy web application to serve these files to clients requesting them. Instead, we can use a program that serves files, such as <a href="https://nginx.org/" target="_blank" rel="noopener noreferrer">nginx<OutboundLink/></a>. Run nginx (or whichever similar program you prefer) in a new container to serve the static files for your SPA.</p>
<p>Since web browsers follow the same-origin policy, your REST API needs to support cross-origin resource sharing, so if you haven't added that to it yet, do it now.</p>
<div class="custom-container danger"><p class="custom-container-title">Do not use frontend frameworks</p>
<p>Since there are no indented learning outcomes in the course about learning a frontend framework like Vue, Svelte, Angular or React, you must implement the SPA yourself from scratch. Feel free to use minor client-side JavaScript libraries you need, such as Moment.js to format dates, and CSS frameworks, such as Bootstrap.</p>
</div>
<h2 id="part-11-optional-tasks" tabindex="-1"><a class="header-anchor" href="#part-11-optional-tasks" aria-hidden="true">#</a> Part 11: Optional Tasks</h2>
<p>Here are some optional tasks you must complete if you want to get a grade higher than 3. Remember that completing these extra tasks does not necessarily give you a higher grade, but you have to complete them to have a chance to get a higher grade. Also, do not forget to look through the page <RouterLink to="/courses/web-development-advanced-concepts/project-grading-guidelines/">Project Grading Guidelines</RouterLink>.</p>
<h3 id="a-fancy-website-required-for-grade-4-and-5" tabindex="-1"><a class="header-anchor" href="#a-fancy-website-required-for-grade-4-and-5" aria-hidden="true">#</a> A fancy website (required for grade 4 and 5)</h3>
<p>Do also implement update and delete operations for your resources on the website (this extra task is only relevant your website, and not your REST API).</p>
<h3 id="a-fancy-spa-required-for-grade-4-and-5" tabindex="-1"><a class="header-anchor" href="#a-fancy-spa-required-for-grade-4-and-5" aria-hidden="true">#</a> A fancy SPA (required for grade 4 and 5)</h3>
<p>In your single-page application, when you have sent an HTTP request to the REST API and wait for an HTTP response, show a loading indicator of some kind. When forms are submitted, users should not be able to submit the form again while you are waiting for the response.</p>
<p>To verify that it works as it should, you need simulate latency, so it takes a couple of seconds before you get back an HTTP response. This can be done using the <a href="https://www.w3schools.com/jsref/met_win_settimeout.asp" target="_blank" rel="noopener noreferrer">setTimeout()<OutboundLink/></a> function in your JavaScript code, or (even better) by telling the web browser to simulate it for you. The blogpost <a href="https://blog.nightly.mozilla.org/2016/11/07/simulate-slow-connections-with-the-network-throttling-tool/" target="_blank" rel="noopener noreferrer">Simulate slow connections with the network throttling tool<OutboundLink/></a> shows how to do it in Firefox.</p>
<h3 id="supporting-third-party-authentication-required-for-grade-5" tabindex="-1"><a class="header-anchor" href="#supporting-third-party-authentication-required-for-grade-5" aria-hidden="true">#</a> Supporting third-party authentication (required for grade 5)</h3>
<p>Let users create a new account on your platform by using their existing account on another platform supporting OpenID Connect instead creating yet another username and password. You can for example use Google.</p>
<p>This does only have to work either through the website or through the REST API. You choose which one if you don't want to implement both, and that should of course be described in your report.</p>
<h2 id="part-12-presentation" tabindex="-1"><a class="header-anchor" href="#part-12-presentation" aria-hidden="true">#</a> Part 12: Presentation</h2>
<p>Present how your platform works to the rest of your class. The reason for the presentation is two-folded:</p>
<ul>
<li>You get some practice in presenting your work, which is a very important skill in your future professional career.</li>
<li>You get to see a variety of different platforms implemented in a very similar same (hopefully not all platforms is about solving the same problem).</li>
</ul>
<p>Your presentation will not be graded; consider it as (mandatory) practice.</p>
<p>Join one of the <a href="https://ju.instructure.com/courses/4775/groups" target="_blank" rel="noopener noreferrer">Project Presentation Sessions<OutboundLink/></a> on Canvas (first come, first served). Only one in each project work pair should join the Project Presentation Session you want to join. Which session you join determines when you should present your work. You will only present to the rest of the project groups in the same presentation session as you.</p>
<p>At the presentation, you should:</p>
<ul>
<li>Explain what the platform you have created is about, including (basically the Introduction chapter in the report):
<ul>
<li>Why would someone use it?</li>
<li>What can the platform be used for?</li>
<li>How have you made the platform as good as possible (so users won't use any other solution)?</li>
<li>Explain the architecture of the platform, including (basically the Architecture chapter in the report, show figures!).</li>
<li>How the solution/platform is implemented. Give an overview.</li>
</ul>
</li>
<li>Give an overview of the implementation of each component the architecture consists of (basically an overview of the rest of the chapters in the report). Use figures!</li>
<li>Give a demonstration of how your solution works. A good idea is that one of you tell the other one what do to (e.g. <em>Go ahead and create a new account for me.</em>), and then the other one shows how to do that.</li>
</ul>
<p>You may use at most 15 minutes, so come prepared. You will not have time for <em>Oh, what more can I show you?</em> or <em>Wait, let me try to remember/look up how we did that</em>, so rehearse the presentation in advance. Be prepared to answer any question about your solution the audience might have.</p>
<p>The examiner will approve you on the Canvas assignment <a href="https://ju.instructure.com/courses/4775/assignments/25248" target="_blank" rel="noopener noreferrer">Presentation<OutboundLink/></a> after your presentation.</p>
